<script>
import { ref, onMounted } from "vue"
import Header from "../../components/global/header/HeaderArea.vue"
import { Toast } from "../../components/global/swal"
import { getUrlId, errorHandle, monthHandle } from "@/utils/handle"
import {
  apiGetCaretakerInfo,
  apiDeleteFavCaretaker,
  apiAddFavCaretaker,
  apiGetWorkOrderList,
  apiSendWorkOrder,
} from "@/api/api"

export default {
  setup() {
    const urlId = getUrlId().urlId
    const workOrderOption = ref("1")

    let caretakerData = ref([])
    let workOrderList = ref([])
    let workOrderList2 = ref([])
    let workOrderListAll = ref([])
    let sendInviteData = ref({
      workOrderId: "",
      caretakerResumeId: [urlId],
    })
    let starClass = ref([])

    onMounted(async () => {
      try {
        // call api
        caretakerData.value = await apiGetCaretakerInfo({ id: urlId })
        caretakerData.value = caretakerData.value.data

        //languageInfo
        caretakerData.value.languageInfo = []
        caretakerData.value.language.forEach((item) => {
          caretakerData.value.languageInfo.push(item.name)
        })
        //experienceInfo
        caretakerData.value.experienceInfo = []
        if (caretakerData.value.experience.length > 0) {
          caretakerData.value.experience.forEach(function (item, index) {
            caretakerData.value.experienceInfo.push(item.careExperience)
          })
        }

        //servicePlaceInfo
        caretakerData.value.workPlaceInfo = []
        caretakerData.value.workPlace.forEach(function (item, index) {
          caretakerData.value.workPlaceInfo.push(item.city + " " + item.area)
        })

        //timePeriod工作時段
        caretakerData.value.timePeriodInfo = []
        caretakerData.value.timePeriod.forEach((item) => {
          caretakerData.value.timePeriodInfo.push(item.name)
        })
        caretakerData.value.timePeriodInfo.reverse()
        if (caretakerData.value.otherJobTime != 0) {
          caretakerData.value.timePeriodInfo.push(
            `${caretakerData.value.otherJobTime}小時`
          )
        }

        //star
        let star = Math.round(caretakerData.value.star)
        for (let i = 0; i < star; i++) {
          starClass.value.push("fas fa-star active")
        }
        for (let i = 0; i < 5 - star; i++) {
          starClass.value.push("fas fa-star")
        }

        // 邀約
        loadWorkOrderList(workOrderOption.value)
      } catch (error) {
        console.log(error)
      }
    })

    const addFav = async () => {
      if (caretakerData.value.isFavorite) {
        let msg = await apiDeleteFavCaretaker({
          id: caretakerData.value.resumeId,
        })
        Toast("已移除最愛")
        caretakerData.value.isFavorite = !caretakerData.value.isFavorite
      } else {
        let msg = await apiAddFavCaretaker({ id: caretakerData.value.resumeId })
        Toast("已加入最愛")
        caretakerData.value.isFavorite = !caretakerData.value.isFavorite
      }
    }
    const handleChangeOption = (workOrderOption) => {
      // 選擇改變時 清除id
      sendInviteData.value.workOrderId = ""
      loadWorkOrderList(workOrderOption)
    }
    const loadWorkOrderList = async (workOrderStatus = "1") => {
      if (workOrderStatus == "1") {
        workOrderList.value = await apiGetWorkOrderList({
          status: 1,
          page: 1,
          limit: 1000,
        })

        workOrderList.value = workOrderList.value.data.list
        workOrderListAll.value = workOrderList.value
      }
      if (workOrderStatus == "2") {
        workOrderList2.value = await apiGetWorkOrderList({
          status: 2,
          page: 1,
          limit: 1000,
        })

        workOrderList2.value = workOrderList2.value.data.list
        workOrderListAll.value = workOrderList2.value
      }
      // 有篩選所以不用拼接
      // workOrderListAll.value = workOrderList.value.concat(workOrderList2.value)

      workOrderListAll.value = workOrderListAll.value.filter(
        (x) =>
          x.serviceRequirementName ==
          caretakerData.value.serviceRequirement.name
      )
      workOrderListAll.value.forEach((item) => {
        let res = monthHandle(item.dates)
        item.newDates = res.newDates
        item.info = res.info
        item.time = res.time
        item.monthInfo = Object.keys(item.info)
      })
    }

    const sendInvite = async () => {
      if (!sendInviteData.value.workOrderId) {
        Toast("請先選擇工單")
        return
      }
      let msg = await apiSendWorkOrder(sendInviteData.value)
      if (msg.status == true) {
        if (msg?.data?.includes("重複邀請")) {
          Toast("重複邀請")
        } else {
          Toast("已發送邀約")
        }
      } else {
        let errorMsg = errorHandle(msg)
        Toast(errorMsg)
      }
    }

    return {
      caretakerData,
      addFav,
      loadWorkOrderList,
      workOrderListAll,
      sendInvite,
      sendInviteData,
      starClass,
      workOrderOption,
      handleChangeOption,
    }
  },
  components: {
    Header,
  },
}
</script>

<template>
  <Header title="看護內容" backToPath="/caregiver/list" />
  <div class="caregiverInfo" v-if="caretakerData.name">
    <div class="imgArea">
      <img
        :src="caretakerData.avatarUrl"
        alt="avatarUrl"
        class="card-img"
        v-if="caretakerData.avatarUrl"
        onerror="this.onerror=null; this.src='https://fakeimg.pl/150x200/'"
      />
      <img
        src="@/assets/image/noavatar.jpg"
        alt="avatarUrl"
        class="card-img"
        v-if="!caretakerData.avatarUrl"
      />
    </div>
    <div class="titleArea">
      <div class="title">{{ caretakerData.name }}</div>
      <div class="card-rating">
        <span class="average">{{ caretakerData.star }}</span
        ><span class="star"
          ><i :class="starClass[0]"></i><i :class="starClass[1]"></i
          ><i :class="starClass[2]"></i><i :class="starClass[3]"></i
          ><i :class="starClass[4]"></i></span
        ><span class="total">({{ caretakerData.commentAmount }})</span>
        <a
          :class="
            caretakerData.isFavorite == 0
              ? 'btn btn-favorite'
              : 'btn btn-favorite favor'
          "
          ><i class="far fa-heart" @click="addFav()"></i
        ></a>
      </div>
    </div>
    <div class="infoText">
      <div class="main1">
        <div class="title1">性別</div>
        <div class="content1">
          {{
            caretakerData.gender == "female" || caretakerData.gender == "Female"
              ? "女性"
              : "男性"
          }}
        </div>
      </div>
      <div class="main1">
        <div class="title1">年齡</div>
        <div class="content1">{{ caretakerData.age }}歲</div>
      </div>
      <div class="main1">
        <div class="title1">身高</div>
        <div class="content1">{{ caretakerData.height }}公分</div>
      </div>
      <div class="main1">
        <div class="title1">體重</div>
        <div class="content1">{{ caretakerData.weight }}公斤</div>
      </div>
      <div class="main1">
        <div class="title1">年資</div>
        <div class="content1">{{ caretakerData.seniority }}</div>
      </div>
      <div class="main1">
        <div class="title1">國籍</div>
        <div class="content1">{{ caretakerData.countryCode }}</div>
      </div>
      <div class="main1">
        <div class="title1">語言</div>
        <div class="content1">{{ caretakerData.languageInfo.join("、") }}</div>
      </div>
    </div>
    <div class="infoText2">
      <div class="main2">
        <div class="title2">履歷身分</div>
        <div class="content2">{{ caretakerData.serviceRequirement.name }}</div>
      </div>
      <div class="main2">
        <div class="title2">相關資格文件</div>
        <div class="content2">{{ caretakerData.license.join("、") }}</div>
      </div>
      <div class="main2">
        <div class="title2">收費標準</div>
        <div class="content2" v-if="caretakerData.wage.home">
          居家：NT${{ caretakerData.wage.home }}/小時、NT${{
            caretakerData.wage.fullDay.home
          }}/日
        </div>
        <div class="content2" v-if="caretakerData.wage.home">
          醫院：NT${{ caretakerData.wage.hospital }}/小時、NT${{
            caretakerData.wage.fullDay.hospital
          }}/日
        </div>
      </div>
      <div class="main2">
        <div class="title2">照顧經驗及時數</div>
        <div class="content2">
          {{ caretakerData.experienceInfo.join("、") }}
        </div>
        <div class="content2" v-if="caretakerData.experienceInfo.length == 0">
          目前尚無照顧經驗
        </div>
      </div>
      <div class="main2">
        <div class="title2">服務地區</div>
        <div class="content2">{{ caretakerData.workPlaceInfo.join("、") }}</div>
      </div>
      <div class="main2">
        <div class="title2">工作時段</div>
        <div class="content2">
          {{ caretakerData.timePeriodInfo.join("、") }}
        </div>
      </div>
      <div class="main2">
        <div class="title2">自我介紹</div>
        <div class="content2">{{ caretakerData.selfIntroduction }}</div>
      </div>
    </div>
    <hr class="divider" />
    <div class="mt-4b btn-container">
      <div class="row">
        <div class="col">
          <button
            class="btn btn-outline-primary"
            type="button"
            data-bs-toggle="modal"
            data-bs-target="#openOrder"
          >
            工單邀約
          </button>
        </div>
      </div>
    </div>
    <!-- Modal 工單邀約 -->
    <div
      class="modal fade"
      id="openOrder"
      tabindex="-1"
      aria-labelledby="openModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-body">
            <div class="form-container p-0">
              <div class="nodata" v-if="workOrderListAll?.length == 0">
                <!-- {{ `無符合該類別(${caretakerData.serviceRequirement.name})工單`
                }}<br /> 
                請先至工單管理中新增工單-->

                <h4>目前無資料</h4>
                <h6>
                  可能的原因:<br />
                  1.沒有將看護加到最愛<br />
                  2.最愛看護中無符合該工單職業_<span class="text-danger">{{
                    `${caretakerData.serviceRequirement.name}`
                  }}</span
                  ><br />
                  3.履歷都正在審核中<br />
                </h6>
              </div>
              <!-- form start -->
              <form v-if="workOrderListAll?.length != 0">
                <div class="row d-flex justify-content-center align-center">
                  <div class="col-10 mt-4 mb-2">
                    <select
                      class="col form-control form-select border-radius-10"
                      required
                      v-model="workOrderOption"
                      @change="handleChangeOption(workOrderOption)"
                    >
                      <option value="1" selected>新工單</option>
                      <option value="2">已回覆工單</option>
                    </select>
                  </div>
                </div>
                <div class="col">
                  <div class="input-list">
                    <div
                      class="form-check"
                      v-for="item in workOrderListAll"
                      :key="item.id"
                    >
                      <input
                        type="radio"
                        class="form-check-input"
                        :value="item.id"
                        :id="item.id"
                        v-model="sendInviteData.workOrderId"
                      />
                      <label class="form-check-label" :for="item.id">
                        <div class="order-container">
                          <div class="card">
                            <div class="card-body">
                              <div class="card-title">
                                {{ item.serviceRequirementName }}
                              </div>
                              <div class="card-date">
                                <div class="d-flex">
                                  <div class="col-left">照顧日期：</div>
                                  <div class="col-right">
                                    {{ item.monthInfo[0] }}/{{
                                      item.info[item.monthInfo[0]].join("、")
                                    }}
                                  </div>
                                  <div
                                    class="col-right"
                                    v-if="item.monthInfo.length > 1"
                                  >
                                    {{ item.monthInfo[1] }}/{{
                                      item.info[item.monthInfo[1]].join("、")
                                    }}
                                  </div>
                                </div>
                              </div>
                              <div class="card-time">
                                照顧時段：{{ item.time }}
                              </div>
                            </div>
                          </div>
                        </div>
                      </label>
                    </div>
                  </div>
                </div>
              </form>
              <!-- form end -->
            </div>
          </div>
          <div class="modal-footer">
            <div class="btn-container">
              <div class="row">
                <div class="col" v-if="workOrderListAll?.length != 0">
                  <button
                    class="btn btn-outline-primary"
                    type="button"
                    data-bs-dismiss="modal"
                    @click="sendInvite"
                  >
                    確定邀約
                  </button>
                </div>
                <div class="col">
                  <button
                    class="btn btn-outline-primary"
                    type="button"
                    data-bs-dismiss="modal"
                  >
                    取消關閉
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.border-radius-10 {
  border-radius: 10px;
}

.caregiverInfo {
  // background-color: aquamarine;
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
  min-height: calc(100vh - $footer-height - 15px - $header-height);
  padding-left: 22px;
  padding-right: 22px;
}

.imgArea {
  display: flex;
  justify-content: center;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;

  img {
    height: 12.5rem;
    width: 9.375rem;
    border-radius: 10px;
    object-fit: cover;
  }
}

.titleArea {
  display: flex;
  justify-content: space-between;

  .card-rating {
    .average {
      color: #777;
      font-size: 0.8125rem;
    }

    .star {
      margin-left: 0.25rem;
      vertical-align: 0.0625rem;

      i {
        margin-left: 0.1875rem;
        color: #ccc;
        font-size: 0.75rem;

        &.active {
          color: #fbbc04;
        }
      }
    }

    .total {
      margin-left: 0.375rem;
      color: #777;
      font-size: 0.8125rem;
    }
  }

  a {
    width: 0.75rem;
    font-size: 1.25rem;
    margin-left: 0.75rem;
  }

  .favor {
    i {
      color: #ff345f;
    }

    .far {
      font-weight: 900;
    }
  }
}

.title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #000000;
  margin-bottom: 16px;
}

.main1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 5px;
  margin-bottom: 5px;

  .title1 {
    font-size: 14px;
    font-weight: 400;
    color: #909090;
    margin-right: 14px;
  }

  .content1 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.main2 {
  margin-top: 10px;
  margin-bottom: 10px;

  .title2 {
    font-size: 14px;
    font-weight: 400;
    color: nth($secondary-color, 1);
  }

  .content2 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.input-list {
  max-height: 60vh;
  overflow-y: scroll;
}

.nodata {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0.8rem;
}
</style>
